export function formatMin(time) {
	if (time) {
		let m = Math.floor(time / 60)
		let s = time % 60
		console.log(m, s)
		if (m > 0 && s > 0) {
			return (m >= 10 ? m : '0' + m) + ':' + (s >= 10 ? s : '0' + s)
		} else if (m > 0) {
			return m >= 10 ? m : '0' + m
		} else {
			return s + ''
		}
	} else {
		return ''
	}
}

export function jPush(str) {
	if (str.type == 1) { // 
		// "subType",":4-24-,25-,26-"
		if (str.subType == 4) {
			uni.navigateTo({
				url: '/pages/message/systemnotice?num=1'
			})
		} else if (str.subType == 24) {
			uni.navigateTo({
				url: '/pages/message/marketing?num=1'
			})
		} else if (str.subType == 25) {
			uni.navigateTo({
				url: '/pages/message/productstatus?num=1'
			})
		} else if (str.subType == 26) {
			uni.navigateTo({
				url: '/pages/order/orderdetail?id=' + str.id
			})
		}
	} else if (str.type == 2) { // 
		uni.navigateTo({
			url: '/pages/my/webview?url=' + str.url
		})
	}
}

/**
 * [chooseImage ]
 * @param  {[url]}  
 * @param  {[key]}  KEY
 * @param  {[token]}  token
 * @param  {[success]}  
 * @return {[type]}      [description]
 */
export function uploadImage(url, key, count = 1, token, success, fail) {
	let loadBusy = false
	if (loadBusy) return
	loadBusy = true
	setTimeout(() => {
		loadBusy = false
	}, 2000)
	uni.chooseImage({
		count: count, //9
		sizeType: ['original', 'compressed'], //
		sourceType: ['camera', 'album'],
		success: res => {
			const tempFilePaths = res.tempFilePaths;
			uni.showLoading({
				title: '',
				mask: true
			})
			for (var i = 0; i < tempFilePaths.length; i++) {
				let uploadTask = uni.uploadFile({
					url,
					filePath: res.tempFilePaths[i],
					name: 'file',
					header: {
						Authorization: token
					},
					formData: {
						key: key
					},
					success: res => {
						let data = JSON.parse(res.data)
						success && success(data)
						uni.hideLoading()
					},
					fail: error => {
						uni.hideLoading()
						fail && fail()
						console.log(error)
					}
				})
			}
		}
	})
}


